  consult() {
    window.location.href = 'https://www.wjx.top/jq/41711237.aspx'
  }
  //数字自增到某一值动画参数（目标元素,自定义配置
  numberAutoPlusAnimation(targetEle, params) {
    const options = params || {}
    let $this = document.querySelector(targetEle),
    time = options.time, //总时间--毫秒为单位
    finalNum = options.num, //要显示的真实数值
    regulator = options.regulator || 100, //调速器，改变regulator的数值可以调节数字改变的速度
    step = finalNum / (time / regulator), /*每30ms增加的数值--*/
    count = options.initial, //计数器
    initial = options.initial;
    const timer = setInterval(() => {
      count = count + step;
      if(count >= finalNum) {
        clearInterval(timer);
        count = finalNum;
      }
      //t未发生改变的话就直接返回 
      //避免调用text函数，提高DOM性能
      var t = Math.floor(count);
      if(t === initial) return;
      initial = t;
      $this.innerHTML = initial;
    })
  }

  numberAnimation() {
    this.numberAutoPlusAnimation('.num1', { 
      time: 2000,
      num: 33,
      initial: 17,
      // regulator: 3,
      regulator: parseInt(Math.random() + 5)
    })
    this.numberAutoPlusAnimation('.num2', { 
      time: 2000,
      num: 50,
